<template>
	<view>
		<view class="uni-list">
			<form>
				<checkbox-group @change="checkboxChange" style="padding-left: 20rpx;">
					<label class="uni-list-cell uni-list-cell-pd" v-for="item in items" :key="item.value">
						<view>
							<checkbox :value="item.value" :checked="item.checked" />
						</view>
						<view>{{item.name}}</view>
					</label>
				</checkbox-group>
				<button class="form-btn" type="primary">提交</button>
			</form>
		</view>
	</view>
</template>

<script>
	export default {
		data() {
			return {
				items: [{
						value: 'none',
						name: '无不良反应',
						checked: "true"
					},
					{
						value: 'CHN',
						name: '体重变化(近一个月内体重明显增加或减少)',
					},
					{
						value: 'BRA',
						name: '镇静睹睡(犯困、总是想睡觉)'
					},
					{
						value: 'JPN',
						name: '头痛眩晕'
					},
					{
						value: 'ENG',
						name: '疲劳乏力(浑身没劲)'
					},
					{
						value: 'FRA',
						name: '恶心呕吐'
					},
					{
						value: '1',
						name: '皮肤过敏(扶摇初期出现大量皮肤红斑、水泡、丘疹)'
					},
					{
						value: '2',
						name: '烦躁易怒(心情烦躁.容易生气、发怒打人等)'
					}
				]
			}
		},
		methods: {
			checkboxChange: function(e) {
				var items = this.items,
					values = e.detail.value;
				for (var i = 0, lenI = items.length; i < lenI; ++i) {
					const item = items[i]
					if (values.includes(item.value)) {
						this.$set(item, 'checked', true)
					} else {
						this.$set(item, 'checked', false)
					}
				}
			}
		}
	}
</script>

<style>
	.uni-list{
		margin-top: 50rpx;
	}
	.uni-list-cell {
		font-size: 28rpx;
		line-height: 90rpx;
	}
	checkbox{
		position: absolute;
		right: 0;
	}
	.form-btn{
		margin-top: 200rpx;
		width: 80%;
	}
</style>
